    /** */
    IdiomData: string[] = null;
    /** */
    IdiomCurrent: string = "";
    /** */
    IdiomIndex: number = 0;
    /** */
    IdiomCompletedQuantity: number = 0;
    /** */
    IdiomMoveing: boolean = false;

    /** */
    InitIdiomData() {
        this.GetIdiomData();
        this.IdiomCurrent = "";
        this.IdiomIndex = 0;
        this.IdiomCompletedQuantity = 0;
        this.IdiomMoveing = false;
    }
    /** */
    CheckIdiomProgress(Char: string): { isValid: boolean, isComplete: boolean } {
        // 
        const Candidate = this.IdiomCurrent + Char;
        // 
        const isComplete = this.IdiomData.indexOf(Candidate);
        // 
        const isValid = this.IdiomData.some(Idiom => Idiom.startsWith(Candidate));
        if (isValid) {
            this.IdiomIndex++;
        }
        if (isComplete !== -1) {
            // 
            this.IdiomCurrent = "";
            this.IdiomCompletedQuantity++;
            return { isValid: true, isComplete: true };
        } else if (isValid) {
            // 
            this.IdiomCurrent = Candidate;
            return { isValid: true, isComplete: false };
        } else {
            // 
            return { isValid: false, isComplete: false };
        }
    }
